<template>
  <section class="ctchain-wrap">
    <div v-loading.fullscreen.lock='loading' :element-loading-text='loadingText'>
      <div class="page-main">
        <h2 class="banner-slogan">创建单个收藏品</h2>
        <h3 class="banner-desc">创建单个收藏夹</h3>
        <div>
          <el-form :model="ParentCollectionForm" ref="ParentCollectionForm" :label-position="labelPosition"
                   label-width="180px" :rules="rulesParent">
            <el-form-item prop="ParentCollectionFile">

              <el-upload class="upload"
                         :show-file-list="false"
                         :limit="1"
                         action="https://jsonplaceholder.typicode.com/posts/"
                         v-model="ParentCollectionForm.ParentCollectionFile"
                         drag
                         accept=".jpg,.jpeg,.png,.bmp,.gif,.JPG,.JPEG,.BMP,.GIF"
                         list-type="application" :on-change="handleChangeParent">
                <div class="upload-inner">
                  <img v-if="imageUrlParent" height="182px" :src="imageUrlParent" class="avatar">
                  <i class="ic-upload"></i>
                  <h3>我们建议您使用至少400x400的图片。Gif也可以。</h3>
                  <el-button type="primary">选择文件</el-button>
                </div>
              </el-upload>

            </el-form-item>
            <el-form-item label="显示名称 (必须的)" prop="tokenName">
              <el-input placeholder="输入令牌名称" v-model="ParentCollectionForm.tokenName"></el-input>
              <p>令牌名称将来无法更改</p>
            </el-form-item>
            <el-form-item label="符号 (必须的)" prop="tokenSymbol">
              <el-input placeholder="输入令牌符号" v-model="ParentCollectionForm.tokenSymbol"></el-input>
            </el-form-item>
            <el-form-item label="描述 (可选的)" prop="tokenDesc">
              <el-input placeholder="传播一些有关您的代币收集的信息" v-model="ParentCollectionForm.tokenDesc"></el-input>
            </el-form-item>
            <!--<el-form-item label="短网址" prop="tokenShortUrl">-->
            <!--<el-input placeholder="输入短网址" v-model="ParentCollectionForm.tokenShortUrl">-->
            <!--<template slot="prepend">nft.ctsign.cn/</template></el-input>-->
            <!--<p>将作为公共URL</p>-->
            <!--</el-form-item>-->
            <div style="text-align: center;margin-bottom: 15px;">
              <el-button @click="createCrc721fun" type="primary">创建收藏夹(ERC721)</el-button>
            </div>
          </el-form>
          <h3 class="banner-desc">创建NFT项目</h3>
          <el-row>
            <el-col :span="4" v-for="(o, index) in contractUrl" :key="o.address" :offset="index > 0 ? 2 : 0">
              <el-card
                :body-style="o.address == contractAddress? { padding: '0px', cursor: 'pointer',  background: '#6e7377' } : { padding: '10px', cursor: 'pointer' }"
                @click="clickContract(o.address)">
                <img :src="o.image" class="image" @click="clickContract(o.address)">
                <div style="padding: 4px;" @click="clickContract(o.address)">
                  <span>{{ o.name }}</span>
                  <div class="bottom clearfix">
                    <el-tooltip class="item" effect="dark" :content="o.address" placement="top">
                      <time class="time">{{ o.address }}</time>
                    </el-tooltip>
                  </div>
                </div>
              </el-card>
            </el-col>
          </el-row>
          <div style="margin-bottom: 10px"></div>
          <el-form :model="SubTokenForm" ref="SubTokenForm" :label-position="labelPosition" label-width="180px"
                   :rules="rulesSub">
            <el-form-item prop="SubTokenFile">

              <el-upload v-model="SubTokenForm.SubTokenFile"
                         class="upload"
                         drag
                         :show-file-list="false"
                         action="ctsign"
                         accept=".jpg,.jpeg,.png,.bmp,.gif,.JPG,.JPEG,.BMP,.GIF,.WEBP,.webp,.mp4,.MP4,.mp3,.MP3"
                         list-type="application"
                         :on-change="handleChangeEvi">
                <div class="upload-inner">
                  <img v-if="imageUrlSub" src="../assets/images/ic_check.png" class="avatar">
                  <i class="ic-upload"></i>
                  <h3>PNG，GIF，WEBP，MP4或MP3，最多30mb。</h3>
                  <el-button type="primary">选择文件</el-button>
                </div>
              </el-upload>

            </el-form-item>
            <el-form-item label="标题 (可选的)" prop="nftTitle">
              <el-input placeholder='例如:"带有徽标的可替换T恤"' v-model="SubTokenForm.nftTitle"></el-input>
            </el-form-item>
            <el-form-item label="作者 (可选的)" prop="author">
              <el-input placeholder="输入作者" v-model="SubTokenForm.author"></el-input>
            </el-form-item>
            <el-form-item label="作者介绍 (可选的)" prop="authorDesc">
              <el-input placeholder="输入作者介绍" v-model="SubTokenForm.authorDesc"></el-input>
            </el-form-item>
            <el-form-item label="作品描述 (可选的)" prop="tokenName">
              <el-input placeholder='例如:"购买后,您可以获得真正的T恤"' v-model="SubTokenForm.nftDesc"></el-input>
              <p>保留换行符</p>
            </el-form-item>
            <!--<el-form-item label="价格" prop="onePrice">-->
            <!--<el-input placeholder="输入一件价格" v-model="SubTokenForm.onePrice"></el-input>-->
            <!--<p>服务费2.5％</p>-->
            <!--<p>您将收到0 ETH $ 0.00</p>-->
            <!--</el-form-item>-->
            <!--<el-form-item label="特许权使用费" prop="banquanFee">-->
            <!--<el-input placeholder='例如:" 10%"' v-model="SubTokenForm.banquanFee"></el-input>-->
            <!--</el-form-item>-->

            <!--<el-form-item label="其他属性" prop="">-->
            <!--<div style="display: flex;">-->
            <!--<el-input placeholder='例如大小' v-model="SubTokenForm.banquanFee"></el-input>-->
            <!--<el-input placeholder='例如M' v-model="SubTokenForm.banquanFee"></el-input>-->
            <!--</div>-->

            <!--</el-form-item>-->
            <div style="text-align: center;">
              <el-button @click="createNftSing" type="primary">创建收藏品</el-button>
            </div>
          </el-form>

        </div>
      </div>
    </div>

  </section>
</template>

<script>
// import {
//   CTCHAIN_LINKS
// } from '@/constant'
import abiSing from '@/assets/abiSing.json'
// const BlocknativeSdk = require('bnc-sdk')
import BlocknativeSdk from 'bnc-sdk'
// create options object
const options = {
  dappId: '0ee58158-e48f-492b-b7df-92554915a6b3',
  networkId: 3363,
  transactionHandlers: [event => console.log(event.transaction)],
  name: 'Ctsign Protocol' // optional, use when running multiple instances
}

// initialize and connect to the api
const blocknative = new BlocknativeSdk(options)
// 单个NFT藏品
const singleByteCode =
  ''

const FormData = require('form-data')
const pinataApiKey = 'b070c3fe783d44f73358'
const pinataSecretApiKey =
  '2afedbefdbac0e729ef41ca6f5d5cd18e53be7586df6fc612df213528bfaddd2'

export default {
  components: {},
  data () {
    return {
      ParentCollectionForm: {
        ParentCollectionFile: null,
        tokenName: '',
        tokenSymbol: '',
        tokenDesc: '',
        tokenShortUrl: ''
      },
      rulesParent: {
        ParentCollectionFile: [{ required: true, message: '验证失败', trigger: 'blur' }],
        tokenName: [{ required: true, message: '验证失败', trigger: 'blur' }],
        tokenSymbol: [{ required: true, message: '验证失败', trigger: 'blur' }]
        // tokenShortUrl: [{ required: true, message: '验证失败', trigger: 'blur' }]
      },
      SubTokenForm: {
        SubTokenFile: null,
        // 作者
        // 作者介绍
        // 作品介绍
        onePrice: '10',
        banquanFee: '',
        nftTitle: '',
        nftDesc: '',
        author: '',
        authorDesc: ''
      },
      rulesSub: {
        SubTokenFile: [{ required: true, message: '验证失败', trigger: 'blur' }]
        // onePrice: [{ required: true, message: '验证失败', trigger: 'blur' }],
        // banquanFee: [{ required: true, message: '验证失败', trigger: 'blur' }],
        // nftTitle: [{ required: true, message: '验证失败', trigger: 'blur' }],
        // nftDesc: [{ required: true, message: '验证失败', trigger: 'blur' }],
        // author: [{ required: true, message: '验证失败', trigger: 'blur' }],
        // authorDesc: [{ required: true, message: '验证失败', trigger: 'blur' }]
      },
      activeName: 'first',
      labelPosition: 'top',
      singleContract: [],
      loading: false,
      loadingText: '加载中',
      contractAddress: '',
      imageUrlParent: '',
      imageUrlSub: '',
      contractUrl: [],
      time: '',
      time1: ''
    }
  },
  async mounted () {
    const time = window.setTimeout(async () => {
      // if (await this.$API.onboard.walletCheck()) {
      if (await this.$API.onboard.getState().address) {
        this.initContractUrl()
        window.clearInterval(time)
      }
    }, 2000)
  },
  methods: {
    async initContractUrl () {
      this.contractUrl = []
      this.singleContract = JSON.parse(localStorage.getItem('singleContract'))
      for (const index in this.singleContract) {
        const collection = new this.$API.web3.eth.Contract(abiSing, this.singleContract[index])
        const resp = await collection.methods.contractURI().call().then(response => response)
        const existingPdfBytes = await fetch(resp, options).then(res => res.json())
        existingPdfBytes.image = existingPdfBytes.image.replace('ipfs:/', 'https://ipfs.daonomic.com')
        existingPdfBytes.address = this.singleContract[index]
        this.contractUrl.push(existingPdfBytes)
      }
    },
    clickContract (contractAddress) {
      console.log(contractAddress)
      this.contractAddress = contractAddress
    },
    createCrc721fun () {
      this.$refs.ParentCollectionForm.validate(valid => {
        if (valid) {
          const that = this
          that.loading = true
          that.loadingText = '加载中'
          const formData = new FormData()
          formData.append('file', this.ParentCollectionForm.ParentCollectionFile)
          this.$axios
            .post('https://api.pinata.cloud/pinning/pinFileToIPFS', formData, {
              maxContentLength: 'Infinity',
              headers: {
                'Content-Type': `multipart/form-data; boundary= ${formData._boundary}`,
                pinata_api_key: pinataApiKey,
                pinata_secret_api_key: pinataSecretApiKey
              }
            })
            .then((response) => {
              // eslint-disable-next-line no-new-object
              const data = new Object()
              data.name = this.ParentCollectionForm.tokenName
              data.description = this.ParentCollectionForm.tokenDesc
              data.image = `ipfs://ipfs/${response.data.IpfsHash}`
              this.$axios
                .post('https://api.pinata.cloud/pinning/pinJSONToIPFS', data, {
                  maxContentLength: 'Infinity',
                  headers: {
                    'Content-Type': 'application/json',
                    pinata_api_key: pinataApiKey,
                    pinata_secret_api_key: pinataSecretApiKey
                  }
                })
                .then(async (metadata) => {
                  // 在这里部署新的合约
                  const collection = new this.$API.web3.eth.Contract(abiSing)
                  const accounts = await this.$API.web3.eth.getAccounts()

                  collection
                    .deploy({
                      data: singleByteCode,
                      arguments: [
                        that.ParentCollectionForm.tokenName,
                        that.ParentCollectionForm.tokenSymbol,
                        that.$API.web3.utils.toChecksumAddress('0x730DC9a056cA478fDef472acFEE3A6672101A6Bc'),
                        `https://ipfs.daonomic.com/ipfs/${metadata.data.IpfsHash}`,
                        (data.external_url =
                          `https://nft.ctsign.cn/${that.contractAddress}/${that.ParentCollectionForm.tokenShortUrl}`)
                      ]
                    })
                    .send({
                      from: accounts[0]
                    }).on('transactionHash', async hash => {
                      that.loadingText = '交易等待上链中'
                      var recpt
                      that.time = window.setInterval(async () => {
                        console.log(await that.$API.web3.eth.getTransactionReceipt(hash))
                        // if (await this.$API.onboard.walletCheck()) {\
                        recpt = await that.$API.web3.eth.getTransactionReceipt(hash)
                        if (recpt.status) {
                          window.clearInterval(that.time)
                          window.clearInterval(that.time)
                          console.log('Transaction is confirmed!')
                          that.loadingText = '交易已确认'
                          that.loading = false
                          that.singleContract.push(recpt.contractAddress) // 将合约地址获取到并且加入到本地缓存
                          localStorage.setItem('singleContract', JSON.stringify(that.singleContract))
                          await that.initContractUrl()
                        } else {
                          that.loadingText = '交易失败'
                          that.loading = false
                        }
                        window.clearInterval(that.time)
                      }, 2000)
                      // alert(hash)
                      // call with the transaction hash of the
                      // transaction that you would like to receive status updates for
                      const { emitter } = blocknative.transaction(hash)

                      // listen to some events
                      emitter.on('txPool', transaction => {
                        console.log(`Sending ${transaction.value} wei to ${transaction.to}`)
                        that.loadingText = '交易确认中'
                      })

                      emitter.on('txConfirmed', transaction => {
                        console.log('Transaction is confirmed!')
                        that.loadingText = '交易已确认'
                        that.loading = false
                        that.$API.web3.eth.getTransactionReceipt(hash).then(ret => {
                          that.singleContract.push(ret.contractAddress) // 将合约地址获取到并且加入到本地缓存
                          localStorage.setItem('singleContract', JSON.stringify(that.singleContract))
                          that.initContractUrl()
                        })
                      })

                      // catch every other event that occurs and log it
                      emitter.on('all', transaction => {
                        console.log(`Transaction event: ${transaction.eventCode}`)
                      // that.$message.error(`Transaction event: ${transaction.eventCode}`)
                      // that.loading = false
                      })
                    })
                })
                .catch((error) => {
                  console.log(error)
                  that.loading = false
                  that.$message.error(error)
                })
            })
            .catch((error) => {
              console.log(error)
              that.loading = false
              that.$message.error(error)
            })
        } else {
          return false
        }
      })
    },
    handleChangeParent (file, fileList) {
      this.ParentCollectionForm.ParentCollectionFile = file.raw
      this.imageUrlParent = URL.createObjectURL(file.raw)
    },

    handleChangeEvi (file, fileList) {
      this.SubTokenForm.SubTokenFile = file.raw
      this.imageUrlSub = URL.createObjectURL(file.raw)
    },
    async createNftSing () {
      if (!this.contractAddress) {
        this.$message.error('请选择收藏夹')
        return
      }

      this.$refs.SubTokenForm.validate(valid => {
        if (valid) {
          const that = this
          this.loading = true
          this.loadingText = '加载中'
          this.$axios
            .get(`https://ctsign.cn/WebV2/open/tokens/${this.contractAddress}/nonce`)
            .then((result) => {
              const collection = new this.$API.web3.eth.Contract(
                abiSing,
                this.contractAddress
              )
              console.log(result.data.data, collection)
              const formData = new FormData()
              formData.append('file', this.SubTokenForm.SubTokenFile)
              this.$axios
                .post('https://api.pinata.cloud/pinning/pinFileToIPFS', formData, {
                  maxContentLength: 'Infinity',
                  headers: {
                    'Content-Type': `multipart/form-data; boundary= ${formData._boundary}`,
                    pinata_api_key: pinataApiKey,
                    pinata_secret_api_key: pinataSecretApiKey
                  }
                })
                .then((response) => {
                  // eslint-disable-next-line no-new-object
                  const data = new Object()
                  data.name = this.SubTokenForm.nftTitle
                  data.description = this.SubTokenForm.nftDesc
                  data.author = this.SubTokenForm.author
                  data.authorDescription = this.SubTokenForm.authorDesc
                  data.image = `ipfs://ipfs/${response.data.IpfsHash}`
                  data.external_url = `https://nft.ctsign.cn/${this.contractAddress}/${result.data.data.value}`
                  this.$axios
                    .post('https://api.pinata.cloud/pinning/pinJSONToIPFS', data, {
                      maxContentLength: 'Infinity',
                      headers: {
                        'Content-Type': 'application/json',
                        pinata_api_key: pinataApiKey,
                        pinata_secret_api_key: pinataSecretApiKey
                      }
                    })
                    .then(async (metadata) => {
                      const accounts = await this.$API.web3.eth.getAccounts()

                      const add = this.$API.web3.utils.toChecksumAddress(
                        accounts[0]
                      )
                      const value = 1
                      const tokenURI = `https://ipfs.daonomic.com/ipfs/${metadata.data.IpfsHash}`
                      const recipient = add
                      collection.methods
                        .mint(
                          result.data.data.value,
                          result.data.data.v,
                          result.data.data.r,
                          result.data.data.s,
                          [
                            [recipient, value]
                          ],
                          tokenURI
                        )
                        .send({
                          from: accounts[0]
                        }).on('transactionHash', hash => {
                          that.loadingText = '交易等待上链中'
                          // alert(hash)
                          var recpt
                          that.time1 = window.setInterval(async () => {
                            console.log(await that.$API.web3.eth.getTransactionReceipt(hash))
                            // if (await this.$API.onboard.walletCheck()) {\
                            recpt = await that.$API.web3.eth.getTransactionReceipt(hash)
                            if (recpt.status) {
                              console.log('Transaction is confirmed!')
                              that.loadingText = '创建收藏品成功'
                              that.loading = false
                            } else {
                              that.loadingText = '交易失败'
                              that.loading = false
                            }
                            window.clearInterval(that.time1)
                          }, 2000)
                          // call with the transaction hash of the
                          // transaction that you would like to receive status updates for
                          const { emitter } = blocknative.transaction(hash)

                          // listen to some events
                          emitter.on('txPool', transaction => {
                            console.log(`Sending ${transaction.value} wei to ${transaction.to}`)
                            that.loadingText = '交易确认中'
                          })

                          emitter.on('txConfirmed', transaction => {
                            console.log('Transaction is confirmed!')
                            that.loadingText = '创建收藏品成功'
                            that.loading = false
                          // alert(that.loadingText)
                          })

                          // catch every other event that occurs and log it
                          emitter.on('all', transaction => {
                            console.log(`Transaction event: ${transaction.eventCode}`)
                          })
                        })
                    })
                    .catch((error) => {
                      console.log(error)
                      that.loading = false
                      that.$message.error(error)
                    })
                })
                .catch((error) => {
                  console.log(error)
                  that.loading = false
                  that.$message.error(error)
                })
            })
        } else {
          return false
        }
      })
    }
  }
}

</script>

